/*FOOTER*/
.footer {
	font-size: 1.6rem;
	line-height: 1.875;
	color: $text-color;
	border-top: 1px solid $border-color; }
.footer__holder {
	overflow: hidden;
	cursor: default;
	padding: 30px 0; }

/*footer bottom*/
.footer-bottom {
	color: $text-color;

	@include media('>desk-hd') {
		.footer-bottom {
			&__col {
				max-width: 73.9%;

				&:last-child {
					max-width: 24.9%; } } } }

	@include media('>tab-sm') {
		.footer-bottom {
			&__col {
				max-width: 59.9%;

				&:last-child {
					max-width: 39.9%; } } } } }

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;

	font-size: 1px;
	line-height: 0;
	letter-spacing: -4px;

	&::after {
		content: "1";
		margin-left: 100%;
		height: 1px;
		overflow: hidden;
		display: inline-block; } }

.footer-bottom__col {
	display: inline-block;
	vertical-align: top;
	font-size: 1.6rem;
	line-height: 1.875;
	letter-spacing: normal;

	&:last-child {
		text-align: right; }

	a {
		color: $text-color;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } } }

/*footer bottom end*/

/*developer*/
.developer {
	display: inline-block;
	padding-right: 4px;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }
.developer__logo {
	display: inline-block;
	vertical-align: top;
	position: relative;
	top: 8px;
	width: 154px;
	height: 20px;
	text-align: left;
	text-indent: -9999px;
	padding-right: 3px;

	&::before, &::after {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 149px;
		height: 16px;
		transition-property: opacity, transform, filter;
		transition-duration: 0.2s; }

	&::before {
		content: '';
		position: absolute;
		left: 4px;
		top: 4px;
		background: url('data:image/png;base64,');
		background-size: 2px;

		.no-touchevents a:hover & {
			transform: translate(0, 1px);
			filter: blur(2px);
			opacity: 0.6; } }

	&::after {
		content: '';
		position: absolute;
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/*developer end*/
/*FOOTER end*/
