@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*base */
html {
	font-size: 62.5%; }

body {
	@include font-normal;
	font-size: $font-size;
	line-height: $line-height;
	-webkit-font-smoothing: antialiased;
	height: 100%;

	color: $text-color;
	background: $bg-color url(../../img/empty.png) repeat fixed;
	min-width: 320px;

	@include media('<mob-lg') {
		@include font($font-size, 1.7); } }

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	@include font-title;
	color: $title-color;
	margin: 0 0 $bottom-space;

	@include media('<mob-lg') {
		margin: 0 0 round-float($bottom-space*0.85, 1); } }

h1 {
	@include font($font-size-h1, $line-height-h1);

	@include media('<mob-lg') {
		@include font($font-size-h1--mob, $line-height-h1--mob); } }

h2 {
	@include font($font-size-h2, $line-height-h2);

	&::before {
		content: 'ยง ';
		display: inline; }

	@include media('<mob-lg') {
		@include font($font-size-h2--mob, $line-height-h2--mob); } }

h3 {
	@include font($font-size-h3, $line-height-h3);

	@include media('<mob-lg') {
		@include font($font-size-h3--mob, $line-height-h3--mob); } }

h4 {
	@include font($font-size-h4, $line-height-h4); }

h5 {
	@include font($font-size-h5, $line-height-h5); }

h6 {
	@include font-normal;
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

.text-title {
	@include font-bold;
	font-size: 1.8rem;
	line-height: $line-height;
	margin-bottom: $bottom-space/2;

	.content & + ul,
	.content & + ol,
	.content & + table {
		margin-top: $bottom-space/2; } }

p {
	padding: 0;
	margin: 0 0 $bottom-space;

	@include media('<mob-lg'); }


hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space; }

a {
	color: $link-color;
	text-decoration: underline;
	cursor: default;
	//outline: none

	&:link {
		cursor: pointer; }

	.no-touchevents &:hover {
		text-decoration: none; }

	&[href^="tel"],
	.no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin: 0 0 $bottom-space;

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }

table {
	border-collapse: collapse;
	border-spacing: 0;
	width: 100%;

	td, th {
		font-size: 1.4rem;
		line-height: 1.3;
		color: $color-table-text;
		padding: 18px 30px;
		box-sizing: border-box;
		border-color: $border-color;

		@include media('<mob-lg') {
			padding: 10px 18px; } }

	th {
		font-size: 1.6rem; } }

b, strong, th {
	@include font-bold; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

//code, pre
//isplay: block
//adding: 8px
//olor: $text-color-light
//ackground-color: $bg-color-light
//ont-size: $font-size-small
//argin: 0 0 $bottom-space

.mark {
	color: $mark-color;
	font-style: inherit;
	font-weight: inherit; }

.text-color-alt {
	color: $text-color-alt; }

.align-center {
	text-align: center; }

.nobr {
	white-space: nowrap; }

.bottom-space {
	margin-bottom: $bottom-space; }

.hide {
	font-size: 0;
	line-height: 0; }

@include media('<desk-hd') {
	.hide-desk-lg {
		display: none !important; } }

@include media('<desk-lg') {
	.hide-desk-md {
		display: none !important; } }

@include media('<desk-md') {
	.hide-desk-sm {
		display: none !important; } }

//ul li pre
//isplay: inline-block
//ertical-align: top
//adding: 0 5px

code.l, code.red, code.green {
	display: inline;
	padding: 0 2px;
	margin: 0; }

.green {
	color: lightseagreen; }

.red {
	color: lightcoral; }

.gray {
	color: #999; }

.warning {
	display: block;
	background-image: url(../../img/icon-warning.svg);
	background-repeat: no-repeat;
	background-position: 10px 10px;
	background-size: 24px;
	padding: 10px 10px 10px 45px;
	//border: 1px solid $base-color
	background-color: rgba($base-color, 0.1); }

.#{$user-content} {
	@each $tags in h2, h3, h4, h5 {
		p + #{$tags},
		ul + #{$tags},
		ol + #{$tags} {
			margin-top: $bottom-space-article;

			@include media('<mob-lg') {
				margin-top: round-float($bottom-space-article*0.85, 1); } } } }

/*base end */

/*list */
.#{$user-content} {
	ul, ol {
		&#{$not-list-classes} {
			margin: $bottom-space 0;
			padding: 0;
			list-style: none;

			li {
				padding-left: 15px;
				margin: #{$bottom-space-string} 0;
				position: relative; } } }

	ul {
		&#{$not-list-classes} {
			& > li:before {
				content: '';
				position: absolute;
				left: 0;
				top: 0.7em;
				height: 0;
				width: 0;
				border-radius: 3px;
				border: 2px solid $text-color; } } }

	ol {
		&#{$not-list-classes} {
			counter-reset: section;

			& > li {
				counter-increment: section;
				height: 1%;
				overflow: hidden;

				&:before {
					position: relative;
					display: inline-block;
					text-align: left;
					@include font-title;
					color: $mark-color;
					content: counter(section);
					padding: 0 4px 0 0;
					min-width: 30px;
					margin-left: -30px;
					box-sizing: border-box; } } } }

	//ol > li > ul
	//ul ul, ul ol, ol ul
	//padding-bottom: 0
	///padding-left: 20px
	///margin: $bottom-space/2 0

	//ul ul > li:before,
	//ul ul > li:first-child:before,
	//ol ul ul > li:before,
	//ol ul ul > li:first-child:before
	//	height: 0
	//	width: 10px
	//	border-width: 2px 0
	//	border-radius: 0
	//	top: 23px

	//ul ul li:first-child,
	//ul ol li:first-child,
	//ol ol li:first-child,
	//ol ul li:first-child
	///padding-top: $space-bottom/2

	//ul ul ul > li:before,
	//ul ul ul > li:first-child:before,
	//ul ol ul > li:before,
	//ul ol ul > li:first-child:before
	//	height: 8px
	//	width: 8px
	//	top: 21px
	//	border-width: 4px
	//	border-radius: 50%

	//list reset
	.list-reset {
		list-style: none;
		margin: 0;

		li, li:first-child {
			padding: 0;

			&:before, &:first-child:before {
				content: none; } } } }
/*list end */

/*table */
.#{$user-content} {
	table {
		margin-bottom: $bottom-space; }

	.table-auto table {
		margin-bottom: 0; }

	.tbl {
		border: none;

		td,
		th {
			color: $color-table-text;
			background-color: $color-table-bg;
			border: 0 solid $border-color;
			font-size: 1.4rem;
			line-height: 1.2;

			&:first-child,
			&:first-child {
				border-left-width: 1px; }

			&:last-child,
			&:last-child {
				border-right-width: 1px; } }

		td {
			border-top-width: 1px; }

		tbody tr:first-child td {
			border-top-width: 0; }

		tbody tr:nth-child(even) td {
			background: #fafafa; }

		th,
		thead td {
			@include font-bold;
			font-size: 1.6rem;
			text-align: left; }

		th,
		thead td,
		tfoot td {
			background: $color-table-bg-alt;
			color: $color-table-text-alt;
			border-color: $color-table-bg-alt;
			border-top-width: 0; } } }
/*table end */

/*blockquote */
.#{$user-content} {
	.blockquote {
		margin: $bottom-space-article 0; }

	blockquote {
		display: block;
		margin: 0;
		padding: 0;

		q {
			display: block;
			position: relative;
			margin: 0;
			padding: 0;
			@include font-normal;
			@include font(2rem, 1.5);
			color: $text-color;
			z-index: 5;

			@include media('<mob-lg') {
				@include font(1.6rem, 1.7); }

			&:before,
			&:after {
				@include pseudo($pos: relative);
				//background: url(../../img/quote-left.svg) no-repeat
				background-size: contain;
				@include size(40px, 35px);
				z-index: -1; }

			&:before {
				//content: 'ยซ'
				content: '';
				margin: 0 0 20px 30%;

				@include media('<mob-lg') {
					margin-bottom: 10px;
					margin-left: -10px; } }

			&:after {
				//content: 'ยป'
				content: '';
				margin: 20px 30% 0 auto;
				transform: rotate(180deg);

				@include media('<mob-lg') {
					content: none;
					display: none; } } }

		cite {
			text-align: right;
			display: block;
			margin-left: auto;
			@include font-title;
			font-size: $font-size;
			line-height: $line-height;
			color: $text-color; } } }
/*blockquote end */

/*content img */
.imgc {
	text-align: center;
	padding-bottom: $bottom-space-section;
	clear: both; }

.imgl {
	text-align: left;
	float: left;
	margin: $bottom-space*0.3 $bottom-space-section $bottom-space-section 0;
	max-width: 50%; }

.imgr {
	text-align: right;
	float: right;
	margin: $bottom-space*0.3 0 $bottom-space-section $bottom-space-section;
	max-width: 50%; }

@include media('<tab-lg') {
	.imgl, .imgr {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: none; } }

/*content img end */

/*view print */
.view-print {
	display: inline-block;
	vertical-align: top;
	padding: 4px 18px;
	background: white;
	font-size: 16px;
	line-height: 30px;
	color: $text-color-alt;
	border: 1px solid $text-color-alt;
	text-decoration: none;
	margin-bottom: $bottom-space;
	transition-property: background-color, color;
	transition-duration: $animation-speed;

	.no-touchevents &:hover {
		background-color: $text-color-alt;
		color: white; }

	&:active {
		transform: scale(0.98); }

	svg {
		width: 21px;
		height: 21px;
		vertical-align: middle;
		margin-right: 10px;

		path {
			fill: $text-color-alt;
			transition-property: fill;
			transition-duration: $animation-speed; } }

	.no-touchevents &:hover path {
		fill: white; }

	span {
		display: inline-block;
		vertical-align: middle; }

	@include media('<tab-lg') {
		font-size: 14px; }

	@include media('<mob-lg') {
		& svg {
			margin-right: 0; }

		& span {
			display: none; } } }
/*view print end*/

/*video */
.video-holder {
	position: relative;
	padding-bottom: 62%;
	padding-top: 25px;
	height: 0; }

.video-holder iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%; }
/*video end */

input[type="radio"], input[type="checkbox"] {
	width: 15px;
	height: 15px; }

/* snippet */
.btn {
	position: relative;
	display: inline-block;
	padding: 6px 12px;
	font-size: 13px;
	font-weight: 700;
	line-height: 20px;
	color: #333;
	white-space: nowrap;
	vertical-align: middle;
	cursor: pointer;
	background-color: #eee;
	background-image: linear-gradient(#fcfcfc, #eeeeee);
	border: 1px solid #d5d5d5;
	border-radius: 3px;
	user-select: none;
	-webkit-appearance: none; }

.btn i {
	font-style: normal;
	font-weight: 500;
	opacity: 0.6; }

.btn .octicon {
	vertical-align: text-top; }

.btn .counter {
	text-shadow: none;
	background-color: #e5e5e5; }

.btn:focus {
	text-decoration: none;
	border-color: #51a7e8;
	outline: 0;
	box-shadow: 0 0 5px rgba(81, 167, 232, 0.5); }

.btn:focus:hover, .btn.selected:focus {
	border-color: #51a7e8; }

.btn:hover, .btn:active, .btn.zeroclipboard-is-hover, .btn.zeroclipboard-is-active {
	text-decoration: none;
	background-color: #ddd;
	background-image: linear-gradient(#eeeeee, #dddddd);
	border-color: #cccccc; }

.btn:active, .btn.selected, .btn.zeroclipboard-is-active {
	background-color: #dcdcdc;
	background-image: none;
	border-color: #b5b5b5;
	box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15); }

.clippy {
	margin-top: -3px;
	position: relative;
	top: 3px;
	vertical-align: baseline; }

.btn[disabled] .clippy {
	opacity: 0.3; }

.snippet {
	position: relative;
	overflow: visible; }

.snippet code {
	font-size: 14px;
	line-height: 20px; }

.snippet .btn {
	transition: opacity .3s ease-in-out;
	opacity: 0;
	padding: 2px 6px;
	position: absolute;
	right: 4px;
	top: 4px; }

.snippet:hover .btn, .snippet .btn:focus {
	opacity: 1; }

@media screen and (max-width: 768px) {
	.snippet .btn {
		opacity: 1; } }

.example + .snippet .btn {
	top: 22px;
	right: 2px; }

/* snippet end */

/* hljs */
.hljs {
	background-color: #f0f0f0; }
/* hljs end */
