/*bitrix default search page*/
.search-page {
	font-size: round-float($font-size*0.85, 1);
	margin-bottom: $bottom-space-section;

	.pagination-wrap {
		margin-bottom: $bottom-space;
		& + br {
			display: none; } }

	.p-nav-title {
		@include font(2.0rem, 1.4);
		@include font-title;
		margin-bottom: $bottom-space; }

	.pagination {
		margin-bottom: $bottom-space;
		@include clear; }

	.p-nav {
		display: inline-block;
		@include size((round-float($font-size*$line-height*1.25, 1)));
		position: relative;
		box-shadow: inset 0 0 0 1px lightgray;
		border-radius: 50%;
		z-index: 10;

		.no-touchevents &:hover {
			background: lightgray; }

		&.disabled {
			opacity: 0.25;
			cursor: default;
			pointer-events: none; }

		.icon {
			position: absolute;
			left: 50%;
			top: 50%;
			width: 16px;
			height: 16px;
			@include margin($top: -(round(16px/2)), $left: -(round(16px/2)));
			background-repeat: no-repeat;
			background-position: center center;
			background-size: contain;

			&.icon-angle-left {
				background-image: url('data:image/png;base64,'); }

			&.icon-angle-right {
				background-image: url('data:image/png;base64,'); } }

		span {
			@include hide-text; } }

	.p-prev {
		float: left;
		margin-right: 10px; }

	.p-next {
		float: right;
		margin-left: 10px; }

	ul {
		overflow: hidden;
		text-align: center;
		cursor: default;
		margin: 0;

		li {
			display: inline-block;
			margin: 0 0 0.5em;
			padding: 0 2px;
			text-align: center;

			&::before {
				content: none; } }

		a {
			display: inline-block;
			vertical-align: top;
			color: $text-color-alt;
			text-decoration: none;
			cursor: pointer; }

		span {
			display: inline-block;
			vertical-align: top;
			min-width: round-float($font-size*$line-height*1.25, 1);
			height: round-float($font-size*$line-height*1.25, 1);
			line-height: round-float($font-size*$line-height*1.25, 1);
			padding: 0 4px;
			border-radius: 50%;
			transition-property: color, background-color;
			transition-duration: $animation-speed; }

		.active {
			cursor: default;

			span {
				color: white !important;
				background-color: $text-color-alt !important; } }

		.no-touchevents & a:hover span {
			background-color: lightgray; } }

	p {
		@include margin-vertical(round-float(($bottom-space/2), 1)); }

	small {
		color: $text-color-light; } }

/*bitrix default search page end*/
