@import "../../libs/include-media/dist/include-media";

/*LAYOUT*/
.wrapper {
	margin: 0 auto;
	//min-height: 100%
	//height: auto !important
	//height: 100%
	min-height: 100vh;
	overflow: hidden;
	position: relative; }

.main {
	padding: 30px 0 0;
	position: relative;
	clear: both;
	z-index: 4; }

.main__holder {
	width: 100%;
	@include clear; }

.user-content {
	padding: 60px 0; }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	margin: 0 260px $bottom-space-section 340px;
	min-height: 50px;

	background-color: whitesmoke;

	@include media('<desk-lg') {
		margin-right: 0; }

	@include media('<tab-lg') {
		margin-left: 0; } }

.header {
	position: relative;
	z-index: 5;

	background-color: lightgreen; }

.sidebar {
	display: inline;
	float: left;
	margin: 0 0 0 -100%;
	width: 320px;

	background-color: sandybrown;

	@include media('<tab-lg') {
		display: none; } }

.aside {
	display: inline;
	float: right;
	margin: 0 0 0 -240px;
	width: 240px;

	background-color: sandybrown;

	@include media('<desk-lg') {
		display: none; } }

.footer {
	margin: 0 auto;
	position: relative;
	clear: both;
	z-index: 5;

	background-color: lightgreen; }

.max-wrap {
	max-width: $layout-width; }

.layout {
	@include padding-horizontal($layout-space-horizontal);
	@include push-auto;

	@include media('<tab-lg') {
		@include padding-horizontal(0); } }

.max-wrap-text {
	max-width: $layout-text-width; }

.layout-text {
	max-width: $layout-text-width;
	@include padding-horizontal($layout-text-space-horizontal);
	@include push-auto;

	@include media('<tab-lg') {
		@include padding-horizontal($layout-text-space-horizontal-md); }

	@include media('<tab-sm') {
		@include padding-horizontal($layout-text-space-horizontal-sm); } }

.max-wrap-flood {
	max-width: $layout-flood-width; }

.layout-flood {
	max-width: $layout-flood-width;
	@include padding-horizontal($layout-flood-space-horizontal);
	@include push-auto;

	@include media('<tab-lg') {
		@include padding-horizontal($layout-text-space-horizontal-md); }

	@include media('<tab-sm') {
		@include padding-horizontal($layout-text-space-horizontal-sm); } }

/*LAYOUT end*/
