/* navigation */
.nav-wrap {
	will-change: transform, opacity;
	transition-property: transform, opacity;
	transition-duration: 0.2s;
}

@media (max-width: 991px) {
	.nav-wrap {
		position: fixed;
		width: 320px;
		height: 100%;
		left: 0;
		top: 0;
		background-color: lightsteelblue;
		z-index: 9999;
		transform: translate(-100%, 0);
		transform: translateX(-100%);
	}
	.nav-wrap.nav-is-open {
		transform: translate(0, 0);
		transform: translateX(0);
	}
}

.nav {
	position: relative;
}

.nav__list > li, .user-content .nav__list > li {
	padding: 0 10px;
}

.nav__list > li::before, .user-content .nav__list > li::before {
	content: none;
}

.nav__list li {
	position: relative;
}

.nav__list li.has-drop {
	padding-right: 2rem;
}

.nav__list li.has-drop::after {
	content: '';
	position: absolute;
	right: 0.5rem;
	top: 1.2rem;
	height: 0;
	width: 0;
	border: 0 solid transparent;
}

.nav__list > li {
	display: inline-block;
	vertical-align: top;
	margin-right: 2rem;
}

@media (max-width: 991px) {
	.nav__list > li {
		display: block;
	}
}

@media (max-width: 991px) {
	.nav__list > li {
		opacity: 0;
		transform: translate(-5%, 0);
		transform: translateX(-5%);
		transition-property: transform, opacity;
		transition-duration: 0.63s;
	}
}

@media (max-width: 991px) {
	.nav__list > li.show-nav-item {
		opacity: 1;
		transform: translate(0, 0);
		transform: translateX(0);
	}
}

.nav__list > li.has-drop::after {
	border-width: 8px 4px;
	border-top-color: tomato;
}

.nav__drop {
	position: absolute;
	left: 0;
	top: 100%;
	background-color: white;
	border: 1px solid lightgray;
	padding: 2rem;
	width: 320px;
	opacity: 0;
	visibility: hidden;
	z-index: 10;
}

li:hover > .nav__drop {
	opacity: 1;
	visibility: visible;
}

.nav__drop li {
	margin-bottom: 1rem;
	font-size: 1.4rem;
	line-height: 1.5;
}

.nav__drop li.has-drop::after {
	border-width: 4px 8px;
	border-left-color: tomato;
}

.nav__drop .nav__drop {
	top: 0;
	left: 100%;
}

a.nav-opener {
	display: none;
	width: 50px;
	height: 50px;
	position: relative;
	text-decoration: none;
	color: #262626;
	background-color: lightcoral;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	transition-property: background-color;
	transition-duration: 0.2s;
}

@media (max-width: 991px) {
	a.nav-opener {
		display: block;
	}
}

.touchevents a.nav-opener {
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

a.nav-opener > span {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	white-space: normal;
}

a.nav-opener > span:active, a.nav-opener > span:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}

a.nav-opener::before, a.nav-opener::after {
	content: "";
	display: block;
	position: absolute;
	width: 26px;
	height: 3px;
	background-color: white;
	top: 50%;
	left: 50%;
	margin-top: -2px;
	margin-left: -13px;
	will-change: opacity, transform;
	opacity: 0;
	transition-property: transform, opacity;
	transition-duration: 0.2s;
	transition-delay: 0s;
	transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

@media (max-width: 1599px) {
	a.nav-opener::before, a.nav-opener::after {
		width: 22px;
		height: 2px;
		margin-top: -1px;
		margin-left: -11px;
	}
}

a.nav-opener > i {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 26px;
	height: 3px;
	background-color: white;
	margin-top: -2px;
	margin-left: -13px;
	backface-visibility: hidden;
	transition-property: opacity;
	transition-duration: 0.2s;
	transition-delay: 0.1s;
}

@media (max-width: 1599px) {
	a.nav-opener > i {
		width: 22px;
		height: 2px;
		margin-top: -1px;
		margin-left: -11px;
	}
}

a.nav-opener > i::before, a.nav-opener > i::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	width: 26px;
	height: 3px;
	background-color: white;
	backface-visibility: hidden;
	transition-property: transform;
	transition-duration: 0.2s;
	transition-delay: 0.1s;
}

@media (max-width: 1599px) {
	a.nav-opener > i::before, a.nav-opener > i::after {
		width: 22px;
		height: 2px;
	}
}

a.nav-opener > i::before {
	top: -8px;
}

@media (max-width: 1599px) {
	a.nav-opener > i::before {
		top: -6px;
	}
}

a.nav-opener > i::after {
	top: 8px;
}

@media (max-width: 1599px) {
	a.nav-opener > i::after {
		top: 6px;
	}
}

.no-touchevents a.nav-opener:hover {
	background-color: #f3f3f3;
}

.no-touchevents a.nav-opener:hover::before, .no-touchevents a.nav-opener:hover::after {
	background-color: tomato;
}

.no-touchevents a.nav-opener:hover > i {
	background-color: tomato;
}

.no-touchevents a.nav-opener:hover > i::before, .no-touchevents a.nav-opener:hover > i::after {
	background-color: tomato;
}

a.nav-opener.nav-is-open {
	background-color: #fff;
}

a.nav-opener.nav-is-open::before, a.nav-opener.nav-is-open::after {
	transition-delay: 0.1s;
	background-color: tomato;
	opacity: 1;
}

a.nav-opener.nav-is-open::before {
	transform: rotate(45deg) translateZ(0);
}

a.nav-opener.nav-is-open::after {
	transform: rotate(-45deg) translateZ(0);
}

a.nav-opener.nav-is-open > i {
	opacity: 0;
	transition-delay: 0s;
}

a.nav-opener.nav-is-open > i::before, a.nav-opener.nav-is-open > i::after {
	transition-delay: 0s;
}

a.nav-opener.nav-is-open > i::before {
	transform: translate(0, 8px) translateZ(0);
}

@media (max-width: 1599px) {
	a.nav-opener.nav-is-open > i::before {
		transform: translate(0, 6px) translateZ(0);
	}
}

a.nav-opener.nav-is-open > i::after {
	transform: translate(0, -8px) translateZ(0);
}

@media (max-width: 1599px) {
	a.nav-opener.nav-is-open > i::after {
		transform: translate(0, -6px) translateZ(0);
	}
}

.nav-overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
