@import "../utils/functions";
@import "../utils/placeholders";

// medium query
$breakpoints: ('desk-hd': 1920px, 'desk-lg': 1600px, 'desk-ml': 1440px, 'desk-md': 1366px, 'desk-sm': 1200px, 'tab-lg': 992px, 'tab-sm': 768px, 'mob-lg': 640px, 'mob-md': 480px, 'mob-sm': 320px);

// base
$font-size: 1.6rem !default;
$line-height: round-float(24/16, 2) !default;

// =======for mobile========
$font-size--mob: 1.4rem !default;
$line-height--mob: round-float(2.4rem/$font-size--mob, 2) !default;

// ========small size=======
$font-size--sm: 1.4rem !default;
$line-height--sm: round-float(2.4rem/$font-size--sm, 2) !default;
// ========smallest size=======
$font-size--less: 1.2rem !default;
$line-height--less: round-float(2.0rem/$font-size--less, 2) !default;

// =======h1========
$font-size-h1: 4.2rem !default;
$line-height-h1: round-float(5.6rem/$font-size-h1, 2) !default;
// sm ===
$font-size-h1--mob: 2.0rem !default;
$line-height-h1--mob: round-float(3.0rem/$font-size-h1--mob, 2) !default;

// =======h2========
$font-size-h2: 2.4rem !default;
$line-height-h2: round-float(3.0rem/$font-size-h2, 2) !default;
// sm ===
$font-size-h2--mob: 1.8rem !default;
$line-height-h2--mob: round-float(2.8rem/$font-size-h2--mob, 2) !default;

// =======h3========
$font-size-h3: 2.0rem !default;
$line-height-h3: round-float(2.4rem/$font-size-h3, 2) !default;
// sm ===
$font-size-h3--mob: 1.6rem !default;
$line-height-h3--mob: round-float(2.4rem/$font-size-h3--mob, 2) !default;

// =======h4========
$font-size-h4: $font-size !default;
$line-height-h4: $line-height !default;

// =======h5========
$font-size-h5: $font-size--sm !default;
$line-height-h5: $line-height--sm !default;

// =======h6========
$font-size-h6: $font-size--sm !default;
$line-height-h6: $line-height--sm !default;

$bottom-space: round-float($font-size*$line-height, 1) !default;
$bottom-space-string: round-float($font-size*$line-height*0.5, 1) !default;
$bottom-space-article: round-float($font-size*$line-height*1.5, 1) !default;
$bottom-space-section: round-float($font-size*$line-height*2, 1) !default;

$base-color: tomato;
