// ะผะฐะบัะธะผะฐะปัะฝะฐั ัะธัะธะฝะฐ ัะตะบััะพะฒะพะณะพ ะฑะปะพะบะฐ ะฒััะพะดััะตะณะพ ะทะฐ ะฟัะตะดะตะปั ัะตะบััะฐ
