// form
$color-form-text: $text-color-light !default;
$color-form-placeholder: lightgray !default;
$color-form-bg: white !default;
$color-form-border: lightgray !default;
$color-form-border-focus: darken($color-form-border, 20%) !default;
$color-form-error: red !default;
$color-form-success: green !default;
$space-bottom-form: 3rem !default;

// btn
$color-btn-bg: tomato !default;
$color-btn-bg-hover: darken($color-btn-bg, 20%) !default;
$color-btn-text: white !default;

// table color
$color-table-text: $text-color !default;
$color-table-border: tomato !default;
$color-table-bg: white !default;
$color-table-text-alt: white !default;
$color-table-bg-alt: tomato !default;

// product in table
$table__img--size: 70, 70; // width, height (in px). ะ ะบะพะดะต ะฒััะพัะฐ ะฒััะธัะปัะตััั ั ัะพะพัะฝะพัะตะฝะธั ัะธัะธะฝั ะบ ะฒััะพัะต, ะฐ ัะฐะทะผะตั ะธะทะพะฑัะฐะถะตะฝะธะต ะฑะตัะตััั ะฒ ะทะฐะฒะธัะธะผะพััะธ ะพั ัะธัะธะฝั
$table__del--size: 40px; // ัะฐะทะผะตั ะบะฝะพะฟะบะธ (ั ะบัะตััะธะบะพะผ) ะดะปั ัะดะฐะปะตะฝะธั ัะพะฒะฐัะฐ ั ัะฐะฑะปะธัั

// animation
$animation-speed: 0.2s !default;
$animation-speed-mouseleave: 0.35s !default;

// fonts style
$font-norm: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif !default;

@mixin font-normal() {
	font: {
		family: $font-norm;
		weight: normal;
		style: normal; } }

@mixin font-bold() {
	font-weight: bold; }

@mixin font-italic() {
	font-style: italic; }

@mixin font-bold-italic() {
	font-weight: bold;
	font-style: italic; }

@mixin font-title() {
	font-weight: 400; }

@mixin font-alt() {
	font-weight: 400; }
