// pow
// =============================================================================
/// ะะพะทะฒะตะดะตะฝะธะต ะฒ ััะตะฟะตะฝั
///
/// @param {Number, Length} $number - ะงะธัะปะพ
/// @param {Number, Length} $exponent - ะกัะตะฟะตะฝั
/// @return {Number} - ะะฝะฐัะตะฝะธะต `$number` ะฒะพะทะฒะตะดะตะฝะฝะพ ะฒ ััะตะฟะตะฝั `$exponent`

@function pow($number, $exponent) {
	$value: 1;

	@if $exponent > 0 {
		@for $i from 1 through $exponent {
			$value: $value * $number; } }

	@return $value; }

