///
/// @param {Number, Length} $number - ะงะธัะปะพ
/// @param {Number, Length} $n - ะะพะปะธัะตััะฒะพ ะทะฝะฐะบะพะฒ ะฟะพัะปะต ะทะฐะฟัะฝะพะน
/// @return {Number} - ะะฝะฐัะตะฝะธะต ะพะบััะณะปะตะฝะฝะพะต ะดะพ `n` ะทะฝะฐะบะพะฒ ะฟะพัะปะต ะทะฐะฟััะพะน

@function round-float($number, $n) {
	$float: pow(10,$n);

	$value: round($number*$float)/$float;

	@return $value; }
